.masters {
  @include section();
  background-color: var(--background-light-cl);

  @media screen and (max-width: 1199px) {
    --indent: 64px;
  }

  @media screen and (min-width: 768px) {
    @include section(100px, 100px);
  }

  @media screen and (min-width: 1200px) {
    @include section(120px, 120px);
  }
}

.masters__title {
  margin-bottom: 44px;
}

.masters__img {
  margin-bottom: 30px;
}

.masters__subtitle {
  font-weight: 400;
  font-size: 18px;
  line-height: calc(25 / 18);
  color: var(--title-txt-cl);
  text-align: center;
  margin-bottom: 8px;
}

.masters__descr {
  font-size: 14px;
  line-height: calc(19 / 14);
  color: var(--accent-cl);
  text-align: center;
  margin-bottom: 30px;
}
